<template>
  <div class="enroll-preview-container">
    <el-card style="min-height:600px">
      <div class="works-preview">
        <div class="title">
          <p>{{detailObj.title}}</p>
   
        </div>
        <el-divider></el-divider>
        <el-row>
          <el-col :span="20" :offset="2">
            <div class="content-main">
              <div class="content-item">
                <div class="sub-title"></div>
                <div class="content" v-html="detailObj.content"></div>
              </div>
              <div class="content-item">
                <div class="sub-title"></div>
                <div class="content">{{detailObj.production}}</div>
              </div>
              <div class="content-item">
                <div class="sub-title"></div>
                <div class="content">{{detailObj.reference}}</div>
              </div>
              <div class="content-item">
                <div class="sub-title"></div>
                <div class="content">{{detailObj.environment}}</div>
              </div>
              <div class="content-item">
                <div class="sub-title"></div>
                <div class="content">{{detailObj.remark}}</div>
              </div>

              <div class="content-item" style="width:50%">
                <div class="sub-title">:</div>
                <div
                  class="content"
                  v-for="(item,index) in detailObj.attachment"
                  :key="index"
                >
                <template v-for="subItm in detailObj.attachment[index]">
                  <file-preview :fileObj="subItm"></file-preview>
                </template>
                </div>
              </div>
              <div class="content-item">
                <div class="sub-title">:</div>
                <div class="content">
                  <file-preview :fileObj="{name:'',url:detailObj.registration}"></file-preview>
                </div>
              </div>
              <el-divider></el-divider>
              <div class="works-detail-main">
                <div class="works-detail-item">{{activityName}}</div>
                <div class="works-detail-item">{{activityProject}}</div>
                <div class="works-detail-item">{{periodList[detailObj.period]}}</div>
                <div class="works-detail-item">{{school}}</div>
                <div
                  class="works-detail-item"
                  v-if="detailObj.author!=undefined"
                >{{detailObj.author.map(item=>{return item.name+'('+item.mobile+')'}).join('')}}</div>
                <div
                  class="works-detail-item"
                  v-if="detailObj.mentor!=undefined"
                >{{detailObj.mentor.map(item=>{return item.name+'('+item.mobile+')'}).join('')}}</div>
                <div class="works-detail-item" >{{detailObj.email}}</div>
              </div>
            </div>
             <el-row>
                <el-col :span="4" :offset="10" style="margin-top:30px;"> 
                <el-button type="primary" @click="submitEnroll()" size="small"></el-button>
                <el-button @click="goBack()" size="small"></el-button>  
                </el-col>
            </el-row>
          </el-col>
        </el-row>
      </div>

    </el-card>
  </div>
</template>
<script>
import WorksPreview from "./WorksPreview";
import FilePreview from "../../components/FilePreview";
export default {
  components: { WorksPreview ,FilePreview},
  data() {
    return {
      detailObj: JSON.parse(this.$route.query.detailObj),
      activityName:this.$route.query.activityName,
      activityProject:this.$route.query.activityProject,
      school:this.$route.query.school,
      periodList: {
        1: "",
        2: "",
        4: "",
        8: "",
        16: "",
        32: "",
        64: ""
      },
    };
  },
  mounted() {
      console.log("this.detailObj===",this.detailObj)
      for(var index in this.detailObj.attachment){
          console.log("this.obj",this.detailObj.attachment[index])
      }
  },
  methods: {
    //
    goBack() {
      this.$router.go(-1);
    },
    //
    async submitEnroll(){
        let params=this.detailObj
        await axiosPost(params)
          .then(res => {
            if (res.code == -1) {
              this.$message({
                message: res.message,
                type: "warning"
              });
            } else if (res.title != undefined && res.title != "") {
              this.$message({
                message: "",
                type: "success"
              });
              setTimeout(() => {
                this.$router.push({
                  path: "/home/submitEnroll"
                });
                
              }, 1000);
            } else {
              that.$router.go(-1);
            }
          })
          .catch(err => err);
    }
  }
};
</script>
<style lang="less" scoped>
.enroll-preview-container {
  width: 1180px;
  margin: auto;
  margin-top: 20px;
}
.works-preview {
  .title {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  .content-main {
    .content-item {
      display: flex;
      flex-direction: column;
      margin-bottom: 30px;
      .sub-title {
        font-size: 16px;
        color: #333;
      }
      .content {
        margin-top: 10px;
        font-size: 14px;
        color: #666;
        line-height: 30px;
      }
    }
  }
  .works-detail-main {
    display: flex;
    flex-direction: column;

    .works-detail-item {
      font-size: 14px;
      color: #333;
      margin-bottom: 10px;
    }
  }
}
</style>